<template>
    <div>
        <div class="p-3">
            <div class="p-3 shadow rounded">
                <p class="">list Report Pegawai</p>
                <hr class="my-2">
                <p class>
                    <select class='form-control' v-model="pegawai">
                        <option v-for="(item, index) in datanya" :key="index+'pegawai'" :value="item.id">{{item.email}}</option>
                    </select>
                </p>
            </div>
            <div v-if="showPegawai">
                <ShowReport :uid="pegawai"/>
            </div>
        </div>
    </div>
</template>
<script>
import firebase from "firebase";
let db = firebase.firestore()
import ShowReport from '@/components/ShowReport.vue'
export default{
    data(){
        return{
            datanya:[],
            pegawai:'',
            showPegawai:false
        }
    },
    watch:{
        pegawai(){
            this.showPegawai=false;
            this.$forceUpdate();
            setTimeout(() => {
                this.showPegawai=true;
                this.$forceUpdate();
            }, 500);
        }
    },
    components:{
        ShowReport
    },
    mounted() {
        this.getAllDataKaryawan()
    },
    methods: {
        getAllDataKaryawan(){
            db.collection('users').onSnapshot(res=>{
                let data = res.docs.map(e=>{
                    return {
                        id:e.id,
                        ...e.data()
                    }
                })
                this.datanya=data;
                this.$forceUpdate()
            })
        }  
    },
}
</script>